:root
{
    --color-bg: #1B2631;
    --color-bg-variant: rgba(26, 82, 118, 0.2);
    --color-primary: #BA4A00;
    --color-primary-variant: rgba(211, 84, 0, 0.4);
    --color-white: #fff;
    --color-light: rgba(208, 211, 212, 0.6);

    --transition: all 0.5s ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}


html
{
    scroll-behavior: smooth;
}

::-webkit-scrollbar
{
    display: none;
}

body
{
    font-family: "Poppins", sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(../src/items/bg-texture.png);
}

/* ESTILOS GENERALES */
.container
{
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5
{
    font-weight: 500;
}

h1
{
    font-size: 2.5rem;
}

section
{
    margin-top: 8rem;
}

section > h2, 
section > h5 
{
    text-align: center;
    color: var(--color-light);
}

section > h2
{
    color: var(--color-primary);
    margin-bottom: 4rem;
}

.text-light 
{
    color: var(--color-light);
}

a
{
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover
{
    color: var(--color-white);
}

.btn
{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover
{
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary
{
    background: var(--color-primary);
    color: var(--color-bg);
}

img
{
    display: block;
    width: 100%;
    object-fit: cover;
}

/*MEDIA QUERIES (MD)*/
@media screen and (max-width: 1024px)
{
    .container
    {
        width: var(--container-width-md);
    }

    section
    {
        margin-bottom: 6rem;
    }
}

/*MEDIA QUERIES (SD)*/
@media screen and (max-width: 600px)
{
    .container
    {
        width: var(--container-width-sm)
    }

    section > h2 
    {
        margin-bottom: 2rem;
    }
}
