header
{
    height: 60vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container
{
    text-align: center;
    height: 100%;
    position: relative;
}

/*CP, BOTONES*/

.cp
{
    margin-bottom: 3rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/*REDES SOCIALES*/
.redes__sociales
{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.9rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.redes__sociales::after
{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/*IMAGEN PERFIL*/
.me
{
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 35rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.scroll__down
{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1rem;
}

/*MEDIA QUERIES (MD)*/
@media screen and (max-width: 1024px)
{
    header
    {
        height: 68vh;
    }
}

/*MEDIA QUERIES (SD)*/
@media screen and (max-width: 600px)
{
    header
    {
        height: 100vh;
    }
    .redes__sociales, .scroll_down
    {
        display: none;
    }
}