.experience__container
{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.experience__container > div
{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover
{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}


.experience__container > div h3
{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
    transition: var(--transition);
}

.experience__container > div:hover h3
{
    color: var(--white);
    font-size: 1.7rem;
}


.experience__content
{
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.experience__details
{
    display: flex;
    gap: 1rem;
    transition: var(--transition);
}

.experience__container > div:hover .experience__details
{
    font-size: 1.2rem;
}

.experience__details-icon
{
    margin-top: 6px;
    color: var(--color-primary);
    transition: var(--transition);
}

.experience__container > div:hover .experience__details-icon
{
    color: rgba(240, 178, 122, 0.4);
    font-size: 1.4rem;
}

.experience__container .text-light
{
    transition: var(--transition);
}

.experience__container > div:hover .text-light
{
    color: var(--color-primary);
}

/*MEDIA QUERIES (MD)*/
@media screen and (max-width: 1024px)
{
   .experience__container
   {
    grid-template-columns: 1fr;
   }

   .experience__container > div
   {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
   }

   .experience__content
   {
    padding: 1rem;
   }
}

/*MEDIA QUERIES (SD)*/
@media screen and (max-width: 600px)
{
    .experience__container
    {
        gap: 1rem;
    }

    .experience__container > div
    {
        width: 100%;
        padding: 2rem 1rem;
    }
}

