.about__container
{
    display: grid;
    grid-template-columns: 27% 50%;
    gap: 15%;
}

.about__me
{
    width: 100%;
    aspect-ratio: 2/2;
    border-radius: 2rem;
    background: linear-gradient(
        45deg, transparent, var(--color-primary), transparent
    );
    display: grid;
    place-items: center;
}

.about__me-image
{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
    max-width: 100%;
    max-height: 103%;
}

.about__me-image:hover
{
    transform: rotate(0);
}

.about__cards
{
    display: grid;
    grid-template-columns: repeat(3, 2fr);
    gap: 1.2rem;
}

.about__card
{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 3rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover
{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__card:hover small
{
    color: white;
    font-size: 0.85rem;
}

.about__card:hover h5
{
    margin-bottom: 1rem;
    color: white;
    font-size: 1rem;
}

.about__card:hover .about__icon
{
    font-size: 1.9rem;
    color: rgba(240, 178, 122, 0.4);
}

.about__icon
{
    color: var(--color-primary);
    font-size: 1.7rem;
    margin-bottom: 1rem;
    transition: var(--transition);
}

.about__card h5
{
    font-size: 0.95rem;
    transition: var(--transition);
}

.about__card small
{
    font-size: 0.7rem;
    color: var(--color-light);
    transition: var(--transition);
}

.about__content p
{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

/*MEDIA QUERIES (MD)*/
@media screen and (max-width: 1024px)
{
    .about__container 
    {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me
    {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about_content p
    {
        margin: 1rem 0 1.5rem;
    }
}

/*MEDIA QUERIES (SD)*/
@media screen and (max-width: 600px)
{
    .about__me
    {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards
    {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content
    {
        text-align: center;
    }

    .about__content p
    {
        margin: 1.5rem 0;
    }
}

